import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Patient } from 'src/app/_models/patient';
import { Router, ActivatedRoute } from '@angular/router';
import { ResponseMessage } from 'src/app/_models/response';
import { environment } from 'src/environments/environment';
import { NotifierService } from 'angular-notifier';
import { AuthService } from 'src/app/auth.service';
import { NotifyService } from 'src/app/shared/notify.service';
import { PatientService } from '../patient.service';
import { AngularEditorConfig } from '@kolkov/angular-editor';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css'],
})
export class ProfileComponent implements OnInit {
  isLoader = false;
  isNotSubscribed = false;
  errorMessage: string = '';

  public showNotification(type: string, message: string): void {
    this.notifier.notify(type, message);
  }

  private notifier: NotifierService;

  @ViewChild('modalContent', { static: true }) modalContent: TemplateRef<any>;

  editorConfig: AngularEditorConfig = {
    editable: true, // This is crucial
    /* Your existing config */
  };
  patientId: string;
  patient: Patient;
  vitals = [];
  verticalVitals = [];
  horizontalVitals = [];
  problems_PastMedicalHistoryValue;
  drugHistoryValue;
  surgicalHistoryValue;
  obstetrics_GynacologyHistoryValue;
  vaccinesValue;
  allergiesValue;
  isButtonVisible = false;
  tab = 1;

  constructor(
    private modal: NgbModal,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    notifier: NotifierService,
    private patientService: PatientService,
    private authService: AuthService,
    private notifyService: NotifyService
  ) {
    this.notifier = notifier;
  }

  ngOnInit() {
    this.activatedRoute.params.subscribe((params) => {
      this.get(params['id']);
      this.patientId = params['id'];
    });
  }

  // Create subscription and redirect to Paystack payment link
  createSubscription() {
    if (!this.patientId) {
      this.showNotification('error', 'Patient ID is not available.');
      return;
    }

    this.isLoader = true;
    this.patientService.createSubscription(this.patientId).subscribe(
      (res: any) => {
        this.isLoader = false;
        console.log(res)
        if (res.status == '200' && res.data.status && res.data.paymentLink) {
          // Open Paystack payment link in a new tab
          window.open( res.data.paymentLink, '_blank');
          this.showNotification('info', 'Redirecting to payment page...');
        } else {
          this.showNotification('error', 'Failed to create subscription. Please try again.');
        }
      },
      (error) => {
        this.isLoader = false;
        this.showNotification('error', 'An error occurred while creating subscription.');
        console.error('Subscription error:', error);
      }
    );
  }
  getTabIndicatorWidth(): number {
    return 80; // Match the min-width of tabs
  }
  getTabIndicatorPosition(): number {
    const tabWidth = 88; // Base width of each tab (80px min-width + 8px padding)
    const padding = 8; // Container padding
    
    let activeTabIndex = 0;
    
    // Determine which tab is active and get its index
    if (this.tab === 1) {
      activeTabIndex = 0;
    } else if (this.tab === 2) {
      // Only count this tab if patient permissions exist
      const hasPatientPermission = true; // Replace with your actual permission check
      activeTabIndex = hasPatientPermission ? 1 : 0;
    } else if (this.tab === 3) {
      // Check if patient tab exists to determine correct index
      const hasPatientPermission = true; // Replace with your actual permission check
      activeTabIndex = hasPatientPermission ? 2 : 1;
    } else if (this.tab === 4) {
      // Check if patient tab exists to determine correct index
      const hasPatientPermission = true; // Replace with your actual permission check
      activeTabIndex = hasPatientPermission ? 3 : 2;
    }
    
    return padding + (activeTabIndex * tabWidth);
  }
  navigateToPatientList() {
    this.router.navigate(['app/patient']);
  }

  get(id) {
    this.isLoader = true;
    this.patientService.getById(id).subscribe(
      (res: ResponseMessage) => {
        console.log(res);
        if (res.status == '200') {
          this.isNotSubscribed = false;
          this.patient = res.data;
          this.problems_PastMedicalHistoryValue = this.patient.problems_PastMedicalHistory;
          this.drugHistoryValue = this.patient.drugHistory;
          this.surgicalHistoryValue = this.patient.surgicalHistory;
          this.obstetrics_GynacologyHistoryValue = this.patient.obstetrics_GynacologyHistory;
          this.vaccinesValue = this.patient.vaccines;
          this.allergiesValue = this.patient.allergies;
          this.isLoader = false;
        } else if (res.status == '403') {
          this.isNotSubscribed = true;
          this.errorMessage = res.data.message;
          this.isLoader = false;
          this.showNotification('error', this.errorMessage);
        } else {
          this.router.navigate(['app/patient']);
          this.isLoader = false;
        }
      },
      (error) => {
        console.log(error);
        this.isLoader = false;
        this.showNotification('error', 'An error occurred while fetching patient data.');
        this.router.navigate(['app/patient']);
      }
    );
  }

  get ImageSrc() {
    if (this.patient) {
      return environment.base_url + this.patient.user.image;
    }
    return environment.base_url + 'images/user.jpg';
  }

  _calculateAge(birthday) {
    if (birthday) {
      birthday = new Date(birthday);
      const ageDifMs = Date.now() - birthday.getTime();
      const ageDate = new Date(ageDifMs);
      return Math.abs(ageDate.getUTCFullYear() - 1970);
    }
    return 0;
  }

  onclickTab(tab) {
    this.tab = tab;
  }

  onClose() {
    this.modal.dismissAll();
  }

  onOpen() {
    this.modal.open(this.modalContent);
  }

  markAsActive() {
    this.authService.toggleactive(this.patient.user._id).subscribe((res: ResponseMessage) => {
      if (res.status === '200') {
        this.patient.user.active = !this.patient.user.active;
        this.notifyService.success(res.data.msg);
      }
    });
  }

  onSaveDiseaseChange(diseaseType, value) {
    this.isButtonVisible = false;
    this.patientService.updateFixedDisease(this.patient._id, { diseaseType, value }).subscribe((res: any) => {
      if (res.status === 200) {
        this.showNotification('success', 'Data Updated Successfully');
      } else {
        this.showNotification('error', 'something went wrong');
      }
    });
  }

  onChnageAccordian() {
    console.log('here');
    this.isButtonVisible = false;
  }

  checkTemprature(t) {
    return t > 37.5 || t < 36.5;
  }

  checkPulseRate(p) {
    return p > 100 || p < 60;
  }

  checkCyclesPerMin(c) {
    return c > 18 || c < 12;
  }

  checkSystolic(s) {
    return s > 139 || s < 90;
  }

  checkDiastolic(d) {
    return d > 89 || d < 60;
  }

  checkOxygen(o) {
    return o > 100 || o < 92;
  }

  goToEdit() {
    this.router.navigate(['/app/patient/edit/', this.patient._id], {
      queryParams: { returnUrl: this.router.url },
    });
  }
}