class DashboardComponent {
    constructor(dashboardService, router) {
        this.dashboardService = dashboardService;
        this.router = router;
        this.isLoader = false;
        this.labReportCount = 0;
        this.appointmentCount = 0;
        this.visitCount = 0;
        this.appointments = [];
    }
    ngOnInit() {
        this.getData();
    }
    getData() {
        this.isLoader = true;
        this.dashboardService.getPatientDashboard().subscribe((res) => {
            if (res.status == '200') {
                this.labReportCount = res.data.labReport;
                this.appointmentCount = res.data.appointment;
                this.visitCount = res.data.visit;
                this.appointments = res.data.appointments;
                this.isLoader = false;
            }
        });
    }
    gotoAppointment() {
        this.router.navigate([`app/calender`]);
    }
}


