class FollowupComponent {
    constructor(activatedRoute, patientService) {
        this.activatedRoute = activatedRoute;
        this.patientService = patientService;
        this.addedPrescriptions = [];
    }
    ngOnInit() {
        this.activatedRoute.params.subscribe(params => {
            this.followUpID = (params['id']);
            if (this.followUpID) {
                this.getFollowUp(this.followUpID);
            }
        });
    }
    // gets follow up from DB
    getFollowUp(id) {
        this.patientService.getSingleFollowUp(id).subscribe((res) => {
            if (res.status === 200) {
                console.log(res);
                this.followUp = res.data[0];
                this.addedPrescriptions = this.followUp.prescription;
            }
        });
    }
}


